@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
/*! minireset.css v0.0.6 | MIT License | github.com/jgthms/minireset.css */
html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button,
input,
select,
textarea {
  margin: 0;
  background: none;
  border: none;
  padding: 0;
  outline: 0;
}



html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

img,
video {
  height: auto;
  max-width: 100%;
}





td:not([align]),
th:not([align]) {
  text-align: left;
}


/* hide scrollbar */
/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.link {
  text-decoration: none;
}

/* font-family: 'VT323', monospace; */
/* font-family: 'Press Start 2P', cursive; */

html {
  background-color: black;
  font-family: "VT323", monospace;
  color: white;
}

.home-content {
  height: 90vh;
  width: 100vw;
  background-color: black;
}

.home-page {
  max-height: 100vh;
  max-width: 100vw;
  overflow-y: hidden;
}

.content-container {
  margin: 5vh 3vw;
  text-align: justify;
  display: flex;
}

.content-container .text {
  line-height: 1.2;
}

.layout-container {
  padding: 1vh 2vw;
  max-width: 800px;
}

p,
button {
  color: white;
  font-family: "VT323", monospace;
}

.button {
  cursor: url(data:image/png;base64,), auto;
}

.link, a {
  text-decoration: none;
  color: white;
}


/* mobile */
@media (max-width: 480px) {
  .content-container {
    line-height: 1;
    flex-direction: column;
  }

  .layout-container {
    width: 94vw;
  }
}

@media (min-width: 481px) and (max-width: 870px) {
  .content-container {
    flex-direction: column;
  }

  .layout-container {
    width: 94vw;
  }
}

.header {
    height: 10vh;
    background-color: black;
    justify-content: flex-end;
    padding-right: 3vw;
    padding-bottom: 3px;
    display: flex;
}

.header .nav {
    display: flex;
    align-items: flex-end;

}

.header .toggle {
    margin-bottom: 4px;
}

.header .menu-button {
    height: 40px;
    color: lightgrey;
}

.menu-button:hover {
    cursor: url(data:image/png;base64,), auto; 
}

.header .nav-link {
    color: lightgray;
    font-family: 'VT323', monospace; 
    font-size: 30px;
    margin-right: 3vw;
    padding: 5px;
}

.nav-link:hover {
    color: white;
    transition: ease-in-out 0.2s;
    /* cursor: url('../assets/cursor1.png'), auto; */
}

@media (max-width: 400px) {
    .header {
        padding-bottom: 7px;
        padding-right: 20px;
    }

    .header .toggle {
        margin-bottom: 12px;
    }

    .header .nav-link {
        font-size: 25px;
    }
}
.game {
    display: flex;
    justify-content: center;
    max-width: 1024px;
    margin: auto;

}

.cell:hover {
    border: 1px solid grey;
    cursor: pointer;
    /* cursor: url('../assets/cursor1.png'), auto; */
}
.grid-wrapper {
    display: flex;
    flex-direction: row;
}

.cols-wrapper {
    display: flex;
    flex-direction: column;
}

.game-menu {
    position: fixed;
    left: 5vw;
    bottom: 5vh;
    display: flex;
}

.game .menu-button {
    color: lightgrey;
    height: 62px;
}

.menu-button img {
    height: 40px;
}

.game .toggle {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.game .intro {
    color: grey;
    font-family: 'VT323', monospace; 
    font-size: 30px;
    padding-left: 1vw;

}

.game .actions {
    padding-left: 1vw;
    display: flex;
}

.actions button,
.actions p {
    color: lightgray;
    font-family: 'VT323', monospace; 
    font-size: 30px;
    margin-right: 2vw;
}

.actions .speed-button {
    padding-left: 4px;
    padding-right: 4px;
    margin-right: 0;
}

.actions #run-button {
    width: 48px;
}


.actions button:hover,
.intro:hover {
    color: white;
    cursor: pointer;
    /* cursor: url('../assets/cursor1.png'), auto; */
    
}

/* mobile */
@media (max-width: 400px) {
    .actions button, .actions p, .game .intro {
        font-size: 20px;
    }

    .actions #run-button {
        width: 32px;
    }

    .menu-button img {
        height: 30px;
    }

}





.about-page .content {
  font-size: 34px;

}

.about-page .content p {
  margin-bottom: 15px;
}

.content-container .text {
  line-height: 1.2;
}

.about-page .buttons {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

}

.layout-container .text {
  max-width: 800px;
}

#start #color-switcher:hover {
  cursor: url(data:image/png;base64,), auto;

}

#setting .story {
  width: 50vw;
}

#setting .play {
  width: 40vw;
  text-align: center;
}

#setting .continue-button {
  margin: auto;
  font-size: 50px;
}

#setting .continue-button:hover {
  color: white; 
}

#setting .choices {
  text-align: left;
}

#setting .choices .prompt p {
  color: #6f8fc3;
}

.content-container .button,
.content-container .confirm-button {
  font-size: 34px;
  padding: 5px 10px;
}


/* mobile */
@media (max-width: 480px) {
  .about-page .content,
  .about-page .button {
    font-size: 24px;
  }
  
  .content-container {
    line-height: 1;
    flex-direction: column;
  }

  #start .text {
    margin-left: 1vw;
    margin-right: 1vw;
  }

  /* #setting is needed here, otherwise it will be overriden */
  #setting .layout-container{
    width: 94vw;
  }
}

/* tablet */
@media (min-width: 481px) and (max-width: 870px) {
  .content-container {
    flex-direction: column;
  }

  #setting .layout-container {
    width: 94vw;
  }
}

.card {
    position: relative;
    margin-right: 20px;
}

.card .card-name p {
    display: inline-block;
}

.card .front,
.card .back{
    color: lightgrey;
    padding: 3px 5px;
}

.card .front:hover,
.card .back:hover {
    color: white;
    cursor: url(data:image/png;base64,), auto;
}

.card .tooltip {
    background-color: black;
    border: 2px dashed white;
    padding: 3px;
    position: absolute;
    font-size: 25px;
    z-index: 2;
}
.tabs .tab-title {
    display: flex;
    align-items: center;
}

.tabs .tab-title .tab-icon {
    margin-right: 4px;
}

.tabs .tab-title:hover {
    cursor: url(data:image/png;base64,), auto;
}

.tabs .tab-text{
    margin-bottom: 0 !important;
}

#journey > .tabs {
    display: flex;
}

.profile > .tabs > .tab-titles {
    display: flex;
    padding: 1vh 2vw;
}

#journey > .tabs > .tab-titles {
    display: flex;
    flex-direction: column;
    margin-top: 3px;
}

.profile>.tabs>.tab-titles .tab-title {
    margin-right: 15px;
    padding: 5px 10px;
    height: 52px;
}

/* .profile>.tabs>.tab-titles .tab-icon img{
    width: 25px;
} */

.profile>.tabs>.tab-titles .tab-icon{
    max-width: 30px;
}

.profile>.tabs>.tab-titles .tab-text:hover {
    color: lightgray;
    transition: ease-in-out 0.2s;
}

#journey > .tabs .tab-titles {
    margin-right: 10px;
}

#journey > .tabs .tab-title {
    width: 34px;
    margin-bottom: 12px;
}


#journey .tabs .tab-content {
    font-size: 24px;
    padding: 5px 10px;

}

#journey .tabs .tab-content .title {
    color: grey;
}

#journey .tabs .inline-link {
    text-decoration: underline;
}

#journey #cs50{
    color: grey;
}

#journey .tabs .inline-link:hover {
    background-color: lightskyblue;
}



/* mobile */
@media (max-width: 480px) {
    #journey .tabs .tab-content {
        font-size: 20px;
    }

    #journey > .tabs {
        flex-direction: column;
    }

    #journey > .tabs > .tab-titles {
        flex-direction: row;
    }

    #journey > .tabs .tab-title {
        margin-right: 12px;
        height: 42px;
    }

    /* .profile>.tabs>.tab-titles .tab-icon{
        max-height: 24px;
    } */

    .profile>.tabs>.tab-titles .tab-text {
        font-size: 20px;
    }

    .profile>.tabs>.tab-titles .tab-title {
        margin-right: 5px;
    }



}
.profile .social-link {
    display: flex;
    align-items: center;
    margin: 20px;
    
}

.profile .social-link img {
    width: 40px;
    height: 100%;
    margin-right: 10px;
}
.profile .social-link p {
    margin-bottom: 0;
    color: gray;

}

.profile .social-link p:hover {
    color: white;
    transition: ease-in-out 0.2s;

}

.profile .social-link a {
    cursor: url(data:image/png;base64,), auto;
}
